<template>
  <div class="processope">
    <div class="topBtn">
      <div class="search">
        <!-- <el-input v-model="taskListPara.keywords" @input="search" placeholder="请输入产品号或任务名">
          <i
            v-show="taskListPara.keywords"
            class="el-icon-close"
            style="margin-top:12px"
            slot="suffix"
            @click="clearInput">
          </i>
        </el-input> -->
      </div>
      <div class="subTaskBtn">
        <!-- <el-button type="primary">移交任务</el-button> -->
      </div>
    </div>
    <el-table
      :data="missionList"
      height="100%"
      border
      stripe
      highlight-current-row
      style="width: 100%">
      <el-table-column
       type="index"
       label="序号"
       align="center"
       width="100">
      </el-table-column>
      <el-table-column
      prop="prodModel"
      label="产品型号"
      align="center"
      width="150">
      </el-table-column>
      <el-table-column
      prop="taskName"
      align="center"
      label="任务名称">
      </el-table-column>
      <el-table-column
      prop="planDay"
      align="center"
      label="计划完成天数">
      </el-table-column>
      <el-table-column
      prop="realDay"
      align="center"
      label="实际耗时天数">
      </el-table-column>
      <el-table-column
      prop="taskStatus"
      align="center"
      label="任务状态">
      </el-table-column>
      <el-table-column
      prop="examiner"
      align="center"
      label="检验员">
      </el-table-column>
      <el-table-column
      prop="examinStatus"
      align="center"
      label="检验状态">
      </el-table-column>
      <el-table-column
      prop="taskCreatDay"
      align="center"
      label="任务创建日期">
      </el-table-column>
      <el-table-column label="操作">
      <template slot-scope="scope" >
        <el-button @click="subTaskBtn(scope.row)" size="mini" type="danger" :disabled="scope.row.examinStatus=='合格'||scope.row.examinStatus=='检验中' || scope.row.taskStatus=='已终止' ?true:false">移交</el-button>
        <el-dialog
         title="提示"
         :visible.sync="subTaskDialogVisible"
         width="30%"
         :before-close="handleClose">
         <span>确定移交？</span>
         <span slot="footer" class="dialog-footer">
           <el-button @click="subTaskDialogVisible = false">取 消</el-button>
           <el-button @click="subTaskSure" type="primary">确 定</el-button>
         </span>
        </el-dialog>
        <el-button @click="leaderSendTaskBtn(scope.row)" v-if="loginId == 3 &&( scope.row.taskStatus == '实施中' || scope.row.examinStatus == '不合格')"  size="mini" type="primary">派发任务</el-button>
        <el-dialog
         title="请选择组员派发任务"
         :visible.sync="leaderSendTaskVisible"
         width="30%"
         :before-close="handleClose">
         
         <el-select v-model="memberOptionsValue" placeholder="请选择派发组员">
              <el-option
                v-for="item in memberOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
          </el-select>
         <span slot="footer" class="dialog-footer">
           <el-button @click="leaderSendTaskVisible = false">取 消</el-button>
           <el-button @click="leaderSubTaskSure" type="primary">派 发</el-button>
         </span>
        </el-dialog>
      </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="taskListPara.currentPage"
      :page-size="taskListPara.pageSize"
      :total="totalDataNum"
      layout="total,prev, pager, next">
    </el-pagination>
  </div>
</template>

<script>
import {SubTask,OperatorTaskList,OperatorMemberList,OperatorLeaderSubTask} from '@/api/request'
import { Message } from 'element-ui'
export default {
    data(){
      return {
          timer:null,
          loginId:0,
          examinerID:0,
          taskListPara:{
            keywords:'',
            currentPage:1,
            pageSize:10,
          },
          totalDataNum:0,
          subTaskDialogVisible:false,
          leaderSendTaskVisible:false,
          subTaskPara:{
            examinerIds:'',
              prodModel:'',
              taskName:'',
              operatorID:'',
          },
          memberOptionsValue:'',
          memberOptions:[],
          leaderSubPara:{
          },
          missionList:[],
          leaderSubProd:'',
          leaderSubTask:''
      }
    },
    mounted(){
      this.timer = setInterval(() => {
        this.getTaskList()
      }, 1000);
      this.isLeader()
    },
    methods:{
      isLeader(){
        this.loginId = JSON.parse(sessionStorage.getItem('auth'))
      },
      clearInput(){
        this.taskListPara.keywords = ''
        this.taskListPara.currentPage = 1
        this.getTaskList()
      },
      // 派发任务
      leaderSendTaskBtn(row){
        this.leaderSendTaskVisible = true
        console.log('派发任务row',row);
        this.subTaskPara.examinerIds = row.examinerIds
        this.leaderSubProd = row.prodModel
        this.leaderSubTask = row.taskName
        OperatorMemberList({'leaderID':JSON.parse(sessionStorage.getItem('userID'))}).then((res) => {
          console.log('组员列表',res);
          if(res.data.result == 1){
            this.memberOptions = res.data.respData
          }
        })
      },
      leaderSubTaskSure(){
        let param = {
          'memberId':this.memberOptionsValue,
          'taskName':this.leaderSubTask,
          'prodModel':this.leaderSubProd,
          'leaderId':JSON.parse(sessionStorage.getItem('userID'))
        }
        OperatorLeaderSubTask(param).then((res) => {
          console.log('组长派发任务res',res);
          if(res.data.result == 1){
            Message.success(`任务派发成功`)
            this.leaderSendTaskVisible = false
          }else{
            Message.error(res.data.msg)
          }
        })
      },
      getTaskList(){
        let param = {
          'keywords':this.taskListPara.keywords,
          'currentPage':this.taskListPara.currentPage,
          'pageSize':this.taskListPara.pageSize,
          'operatorID':JSON.parse(sessionStorage.getItem('userID'))
        }
        OperatorTaskList(param).then((res) =>{
          if(res.data.result == 1){
            this.missionList = res.data.respData.dataDetail
            this.totalDataNum = res.data.respData.totalDataNum
            for(let i = 0;i < this.missionList.length;i++){
              if(this.missionList[i].examinStatus == 1){
                this.missionList[i].examinStatus = '未检验'
              }else if(this.missionList[i].examinStatus == 2){
                this.missionList[i].examinStatus = '合格'
              }else if(this.missionList[i].examinStatus == 3){
                this.missionList[i].examinStatus = '不合格'
              }else if(this.missionList[i].examinStatus == 4){
                this.missionList[i].examinStatus = '检验中'
            }}
            for(let i = 0;i < this.missionList.length;i++){
              if(this.missionList[i].taskStatus == 1){
                this.missionList[i].taskStatus = '未实施'
              }else if(this.missionList[i].taskStatus == 2){
                this.missionList[i].taskStatus = '实施中'
              }else if(this.missionList[i].taskStatus == 3){
                this.missionList[i].taskStatus = '已完成'
              }else if(this.missionList[i].taskStatus == 4){
                this.missionList[i].taskStatus = '延期'
              }else if(this.missionList[i].taskStatus == 5){
                this.missionList[i].taskStatus = '检验中'
              }else if(this.missionList[i].taskStatus == 6){
                this.missionList[i].taskStatus = '已终止'
              }
            }
          }
        })
      },
      subTaskBtn(row){
        this.subTaskDialogVisible = true
        console.log('移交row',row);
        this.subTaskPara.examinerIds = row.examinerIds
        this.subTaskPara.prodModel = row.prodModel
        this.subTaskPara.taskName = row.taskName
        this.subTaskPara.operatorID = JSON.parse(sessionStorage.getItem('userID'))
      },
      subTaskSure(){
        let param ={
          'operatorID':this.subTaskPara.operatorID,
          'examinerIds':this.subTaskPara.examinerIds,
          'prodModel':this.subTaskPara.prodModel,
          'taskName':this.subTaskPara.taskName
        }
        SubTask(param).then((res) =>{
          console.log('移交res',res);
          if(res.data.result == 1){
            Message.success(res.data.msg)
            this.subTaskDialogVisible = false
          }else{
            Message.error(res.data.msg)
          }
        })
      },
      handleClose(done){
        done()
        this.memberOptionsValue = ''
      },
      handleSizeChange(SizeChange){ 
          console.log('SizeChange',SizeChange);
          this.taskListPara.pageSize = SizeChange
          this.getTaskList()
          },
      handleCurrentChange(CurrentPage){ 
        console.log('CurrentPage',CurrentPage);
        this.taskListPara.currentPage = CurrentPage
        this.getTaskList()
      },
      countPageIndex(index){
        return (this.taskListPara.currentPage - 1) * this.taskListPara.pageSize + index + 1
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
        console.log('勾选的这一行的数据',this.multipleSelection);
      },
      search(){
        this.taskListPara.currentPage = 1
        this.getTaskList()
      }
    },
    beforeDestroy(){
      clearInterval(this.timer);
      this.timer = null;
    }
}
</script>

<style lang="scss" scoped>
    .processope{
        width: 100%;
        height: 89%;
        .topBtn{
          display: flex;
          justify-content: space-between;
          margin-bottom: .9rem;
          .search{
            display: flex;
          }
        }
    }
</style>


